olor: #ffffff;\n  padding: 12px 42px;\n  cursor: pointer;\n  transition: 0.45s;\n\n  &--without-bg {\n    background: rgba(255, 255, 255, 0.01);\n    box-shadow: 0px 4px 0px #d57709;\n    border: 2px solid #f7de0d;\n    border-radius: 49px;\n\n    &:hover {\n      box-shadow: none;\n    }\n\n    &:active {\n      background: linear-gradient(180deg, #f7de0d 3.5%, #f7a50d 100%),\n        rgba(255, 255, 255, 0.01);\n      box-shadow: 0px 4px 0px #d57709;\n    }\n\n    &:disabled {\n      background: linear-gradient(180deg, #f7de0d 3.5%, #f7a50d 100%),\n        rgba(255, 255, 255, 0.01);\n      opacity: 0.6;\n    }\n  }\n\n  &--with-bg {\n    background: linear-gradient(180deg, #f7de0d 3.5%, #f7a50d 100%),\n      rgba(255, 255, 255, 0.01);\n    box-shadow: 0px 4px 0px #d57709;\n    border-radius: 49px;\n\n    &:hover {\n      box-shadow: none;\n    }\n\n    &:active {\n      box-shadow: inset -4px -4px 4px rgba(255, 255, 255, 0.45),\n        inset 4px 4px 4px rgba(11, 9, 0, 0.35);\n    }\n  }\n}\n",".text {\n  font-size: 16px;\n  line-height: 145%;\n  opacity: 0.8;\n\n  &--white {\n    color: #ffffff;\n    opacity: 1;\n  }\n}\n",".img-container {\n  height: 400px;\n  display: flex;\n  align-items: center;\n  transition: 0.7s;\n  background: url(\"./../../public/img/horse-bg.png\") no-repeat center center;\n  background-size: contain;\n\n  &:hover {\n    background-image: url(\"./../../public/img/horse-bg--hover.png\");\n  }\n}\n",".main-header {\n  padding: 32px 0 24px 0;\n}\n",".navigation__list {\n  @include list-none();\n  display: flex;\n  flex-wrap: wrap;\n  width: 100%;\n  align-items: center;\n  justify-content: space-between;\n}\n\n.navigation__links-list {\n  @include list-none();\n  width: 29%;\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: space-between;\n}\n\n.navigation__link-item {\n  &--whitepaper {\n    position: relative;\n\n    &:hover .language-settings {\n      border: 2px solid #f8ab0e;\n      padding: 12px 16px;\n      height: 100px;\n  